<template>
  <v-container fluid>
    <v-card elevation="2" :disabled="loadingRecords" :loading="loadingRecords">
      <div class="d-flex">
        <div>
          <v-card-title>Create Account</v-card-title>
          <v-card-subtitle
            ><small
              >Note: All required fields is marked with (*)</small
            ></v-card-subtitle
          >
        </div>
        <v-spacer></v-spacer>
        <router-link :to="{ name: 'Accounts' }" class="mt-5 mr-5"
          >Back</router-link
        >
      </div>
      <v-form ref="form">
        <v-card-text>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="first_name"
                v-model="form.first_name"
                label="First name*"
                :rules="[rules.required, rules.minimum(2), rules.maximum(100)]"
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="middle_name"
                v-model="form.middle_name"
                label="Middle name"
                :rules="[rules.minimum(2), rules.maximum(100)]"
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="last_name"
                v-model="form.last_name"
                label="Last name*"
                :rules="[rules.required, rules.minimum(2), rules.maximum(100)]"
                outlined
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="username"
                v-model="form.username"
                label="Username*"
                :rules="[rules.required, rules.minimum(2), rules.maximum(100), usernameExist]"
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-autocomplete
                ref="department"
                v-model="form.department"
                :rules="[rules.required]"
                :items="departments"
                item-text="name"
                item-value="id"
                label="Department*"
                outlined
                dense
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-select
                ref="role"
                v-model="form.role"
                :rules="[rules.required]"
                :items="roles"
                item-text="name"
                item-value="id"
                label="Role*"
                outlined
                dense
              ></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="email"
                v-model="form.email"
                label="Email*"
                :rules="[rules.required, rules.validEmail, emailExist]"
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field
                ref="employee_number"
                v-model="form.employee_number"
                label="Employee number*"
                :rules="[
                  rules.required,
                  employeeNumberExist,
                  rules.maximum(100),
                ]"
                outlined
                dense
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="pa-4">
          <v-btn
            color="success"
            class="text-capitalize"
            @click="confirmationDialog = true"
          >
            Create
          </v-btn>
          <v-btn class="text-capitalize" :to="{ name: 'Accounts' }">
            Cancel
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onSubmit"
    />
  </v-container>
</template>

<script>
import formRules from "@/helpers/formRules";
import { mapState } from "vuex";
import { isObjectEmpty } from "@/helpers/form";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";

export default {
  components: { ConfirmationMessage },

  data: () => ({
    rules: formRules,
    form: {
      first_name: null,
      middle_name: null,
      last_name: null,
      username: null,
      department: null,
      role: null,
      email: null,
      employee_number: null,
    },
    usernameExistText: null,
    emailExistText: null,
    employeeNumberExistText: null,
    confirmationDialog: false,
    confirmationData: {
      title: "Are you sure you want to create?",
      text: "This action cannot be undone.",
      closeBtnText: "Cancel",
      closeBtnColor: "gray",
      saveBtnText: "Create",
      saveBtnColor: "success",
    },
  }),

  created() {
    this.$store.dispatch("account/createRecord");
  },

  computed: {
    ...mapState("account", [
      "roles",
      "departments",
      "loadingRecords",
      "errors",
    ]),
  },

  methods: {
    usernameExist(val) {
      if (val == this.usernameExistText) {
        return `"${val}" already exist`;
      } else {
        return true;
      }
    },

    emailExist(val) {
      if (val == this.emailExistText) {
        return `"${val}" already exist`;
      } else {
        return true;
      }
    },

    employeeNumberExist(val) {
      if (val == this.employeeNumberExistText) {
        return `"${val}" already exist`;
      } else {
        return true;
      }
    },

    async onSubmit() {
      this.confirmationDialog = false;
      if (this.$refs.form.validate()) {
        await this.$store.dispatch("account/storeRecord", this.form);

        if (isObjectEmpty(this.errors)) {
          this.$refs.form.reset();
        } else {
          this.setErrors();
        }
      }
    },

    setErrors() {
      let errorFocus = "";

      Object.entries(this.errors).forEach(([key, value]) => {
        if (value) {
          if (!errorFocus) {
            errorFocus = key;
          }

          if (key == "username") {
            this.usernameExistText = this.form.username;
          }

          if (key == "email") {
            this.emailExistText = this.form.email;
          }

          if (key == "employee_number") {
            this.employeeNumberExistText = this.form.employee_number;
          }

          this.$refs.form.validate();
          this.$refs[errorFocus].focus();
        }
      });
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },
  },
};
</script>
